/*************************** Spot's options **********************************/

#Spot_color_dialog {
    width: 270px;
    /*height: 464px;*/
    border: 4px solid;
    border-top-color: #E7D2A5;
    border-right-color: #776737;
    border-bottom-color: #776737;
    border-left-color: lightyellow;
    position: absolute;
    top: 60px;
	left: 172px;
    z-index: 200;
    background-color: #EADCBB;
    box-shadow: 30px 10px 5px rgba(0, 0, 0, .5);
    display: block;
    border-radius: 15px;
}

.wraper2 {
    height: inherit;
}

#Spot_color_dialog .header p {
    text-align: center;
    padding: 1px;
    color: yellow;
}

#Spot_color_dialog .header {
    height: 30px;
    background: linear-gradient(to right, darkblue, #4B9BC6);
    border-top-left-radius: 100%;
    border-top-right-radius: 100%;
}

#Spot_color_dialog .wraper2 .header button {
    width: 24px;
    height: 24px;
    float: right;
    top: -29px;
    right: 5px;
    position: relative;
    cursor: pointer;
    background-image: url("../assets/images/close_32x32.png");
    background-color: transparent;
    border: none;
    background-size: contain;
}

.closed {
    display: none;
}

.showen {
    display: block;
}

