/*--------------------------------------------------------------*/

#first-or-second {
    display: inline-block;
    text-shadow: 1px 1px #fff;
    text-decoration: none;
    padding: 4px;
    border: 1px solid #aaa;
    border-radius: 100px;
    outline: none;
    background: #d6dbbf no-repeat;
    background-image:
            linear-gradient(to left, #aaa, #aaa),
            linear-gradient(to left, #feffe8, #feffe8),
            linear-gradient(#feffe8, #d6dbbf);
    background-position: calc(100% - 2.7em) 0, calc(100% - 2.7em + 1px) 0, 0 0;
    background-size: 1px 100%, 1px 100%, auto auto;
    overflow: hidden;
    width: 38px;
    cursor: pointer;
    user-select: none;
    vertical-align: middle;
}

/*--------------------------------------------------------------*/
#Spot_color_dialog > div > div.header > p,
#Player_is,
#Computer_is,
.mytooltiptext
{
    font-family: 'Benne', serif;
    font-weight: 600;
    letter-spacing: 2px;
}

.random_level {
    clear: both;
    text-align: center;
    border: 3px solid;
    border-top-color: #776737;
    border-left-color: #776737;
    border-right-color: #FFFBF3;
    border-bottom-color: #FFFBF3;
    margin: 5px;
    margin-top: 57px;
    padding: 5px 0px 0px;
    font-family: 'Benne', serif;
    font-weight: 600;
}

div.random_level > div:nth-child(1) {
    line-height: 1.3;
    height: 15px;
}

div.random_level > div:nth-child(2) {
    height: 32px;
}

div.random_level > div:nth-child(2) > span {
    top: -2px;
}