.scroll .up {
    width: 100%;
    height: 29px;
    background: url("../assets/images/up.png") no-repeat;
    background-size: 100% 100%;
    background-color: #EADCBB;
    cursor: pointer;
}

.scroll .down {
    width: 28px;
    height: 29px;
    background: url("../assets/images/down.png") no-repeat;
    background-size: 100% 100%;
    background-color: #EADCBB;
    position: absolute;
    bottom: 0px;
    box-sizing: border-box;
    cursor: pointer;
}

.scroll .tracking {
    width: 100%;
    height: 0px;
    background-size: 100% 100%;
}

.scroll .lev-position {
    width: 100%;
    height: 15px;
    background-color: #EADCBB;
    border: 2px black;
    background-size: 100% 100%;
    position: relative;
    top: -3px;
}

#tabs-1 .scroll .up {
    background-image: url("../assets/images/arrow_up.png"), url("../assets/images/Sokoban/sokoban.png");
    background-repeat: no-repeat;
    background-size: inherit;
    background-position: center, -100px 0px;
}

#tabs-1 .scroll .down {
    background-image: url("../assets/images/arrow_down.png"), url("../assets/images/Sokoban/sokoban.png");
    background-repeat: no-repeat;
    background-size: inherit;
    background-position: center, -100px 0px;
}

#tabs-1 .scroll .tracking {
    background: url("../assets/images/scroll_fon.png") no-repeat;
}

#tabs-1 .scroll .lev-position {
    /*background: url("../assets/images/scroll_sokoban__fon.png") no-repeat;*/
    background: url("../assets/images/scroll_sokoban__fon.png");
    /*background-size: 100% 100%;*/
    background-position: center;
}

#tabs-3 .scroll .up {
    background: url("../assets/images/up.png") no-repeat;
}

#tabs-3 .scroll .down {
